.storybook-button {
  font-family: 'Poppins','Nunito Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  border: 0;
  border-radius: 4px;
  cursor: pointer;
  display: inline-block;
  line-height: 1;
  font-size: 14px;
}
.storybook-button--primary1 {
  color: black;
  background-color: #feb30b;
}
.storybook-button--primary1:hover {
  background-color: #b9830c;
}

.storybook-button--primary1:active {
  background-color: #fff0d0;
}
.storybook-button--primary2 {
  color: white;
  background-color: #25272c;
}
.storybook-button--secondary1 {
  color: #25272c;
  background-color: transparent;
  border: 1px solid #feb30b;
}
.storybook-button--secondary1:hover {
  background-color: #fff0d0;
}

.storybook-button--secondary1:active {
  background-color: #ffdf9b;
}
.storybook-button--secondary2 {
  color: #25272c;
  background-color: transparent;
  border: 1px solid #25272c;
}
.storybook-button--sm {
  padding: 4px 16px;
}
.storybook-button--lg {
  padding: 8px 16px;
}
.storybook-button--disable,
.storybook-button--disable:hover,
.storybook-button--disable:active {
  cursor: not-allowed;
  background-color: #eceeef;
  color:#898989;
  border: 0;
}
